import React, { useState, useEffect } from 'react'
import { Link } from "gatsby"
import styles from './cookies.module.scss'
import CookiesLib from 'js-cookie'

const Cookies = () => {
  const [isBanner, set] = useState(false)

  useEffect(() => {
    const hideBanner = CookiesLib.get('hideBanner')
    set(hideBanner ? false : true)
  }, [])

  const hideCookiebanner = () => {
    set(false)
    CookiesLib.set('hideBanner', '1')
  }

  const banner = (
    <div className={styles.cookiebanner} id="cookiebanner">
      <div className="is-flex has-full-height">
      <p className={`is-centered is-hidden-mobile ${styles.cookietext}`}>This site uses cookies to learn what can be improved to provide you with a great experience. 📈🎉 
        <br />If you continue surfing the site, you agree to the <Link to="/privacy/"className="is-underlined">use of cookies</Link>. <button onClick={hideCookiebanner} href="#" className={`is-underlined ${styles.Iagree}`}>I agree</button>.
      </p>
      <p className={`is-centered is-hidden-tablet ${styles.cookietext}`}>This site uses cookies to learn and provide you with a great experience. 📈🎉 
        <br />If you continue surfing the site, you agree to the <Link to="/privacy/" className="is-underlined">use of cookies</Link>. <button onClick={hideCookiebanner} href="#" className={`is-underlined ${styles.Iagree}`}>I agree</button>.
      </p>
      
      <button onClick={hideCookiebanner} className={`button ${styles.cookiebutton}`}><i className="fas fa-times"></i></button>
      </div>
    </div>
  )

  return isBanner ? banner : null
}

export default Cookies